<h1>API</h1>
<h2 id="detail-props"><a class="header-anchor" href="#detail-props" aria-hidden="true">¶</a> detail props</h2>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>labels</td>
<td>配置详情模板信息</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>data</td>
<td>详情数据集合</td>
<td>Object</td>
<td>{}</td>
</tr>
<tr>
<td>labelPosition</td>
<td>设置属性 labelPosition，可以改变表单域标签的位置，left 为左对齐，right 为右对齐，top 会置于表单域顶部</td>
<td>String</td>
<td>top</td>
</tr>
<tr>
<td>columns</td>
<td>label标签名称集合</td>
<td>Object</td>
<td>{}</td>
</tr>
</tbody>
</table>
<h2 id="detail-labels"><a class="header-anchor" href="#detail-labels" aria-hidden="true">¶</a> detail labels</h2>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>titles</td>
<td>详情模板标题数组</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>id</td>
<td>模板配置参数id</td>
<td>Number</td>
<td>--</td>
</tr>
<tr>
<td>colNum</td>
<td>列数对象，col1表示第一列项数，col2表示第二列项数</td>
<td>Number</td>
<td>col1:1,col2:2</td>
</tr>
<tr>
<td>labelPosition</td>
<td>设置属性 labelPosition，可以改变表单域标签的位置，left 为左对齐，right 为右对齐，top 会置于表单域顶部</td>
<td>String</td>
<td>top</td>
</tr>
<tr>
<td>columns</td>
<td>每行列数,两种布局：每行2列或每行3列</td>
<td>Number</td>
<td>2</td>
</tr>
<tr>
<td>column</td>
<td>标签配置对象,键是标签参数，值是标签配置对象</td>
<td>Object</td>
<td>{}</td>
</tr>
</tbody>
</table>
<h2 id="detail-column"><a class="header-anchor" href="#detail-column" aria-hidden="true">¶</a> detail column</h2>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>标签中文名称</td>
<td>String</td>
<td>''</td>
</tr>
<tr>
<td>type</td>
<td>设置标签对应值渲染类型，一共四种：文本类型text，热链接类型link，查看更多dailog类型readMore,图片类型picture</td>
<td>String</td>
<td>--</td>
</tr>
<tr>
<td>isShow</td>
<td>控制标签隐藏显示，true展示，false不展示</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>style</td>
<td>标签内容样式，两种：default为通用样式，red为加红色边框</td>
<td>String</td>
<td>default</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.dome-alert .w-alert:not(:first-child){
  margin-top: 10px;
}
</style>
<script>
export default {
    data(){
        return {
            labels : [
                {
                    titles:['详情标题1','详情标题2'],
                    id:1,
                    colNum:{
                        col1:3,//第一列项数
                        col2:4//第二列项数
                    },
                    labelPosition:'top',//标签位置，四种：top,left,right,bottom
                    columns:2,//每行列数,两种：每行2列或每行3列
                    column:{
                        'name': {
                            label:'名字',
                            type:'text',
                            isShow:true,
                            style:'default'
                        },
                        'address': {
                            type:'link',
                            label:'地址',
                            isShow:true,
                            style:'default'
                        },
                        'more': {
                            type:'readMore',
                            label:'更多信息',
                            isShow:true,
                            style:'default'
                        },
                        'picture': {
                            type:'picture',
                            label:'图片1',
                            isShow:true,
                            style:'red'
                        }
                    }
                },
                {
                    titles:[],
                    id:2,
                    colNum:{
                        col1:2,//第一列项数
                        col2:3//第二列项数
                    },
                    labelPosition:'left',
                    columns:3,
                    column:{
                        'name': {
                            label:'名字',
                            type:'text',
                            isShow:true,
                            style:'default'
                        },
                        'address': {
                            type:'link',
                            label:'地址',
                            isShow:true,
                            style:'default'
                        },
                        'more': {
                            type:'readMore',
                            label:'更多信息',
                            isShow:true,
                            style:'default'
                        },
                        'picture': {
                            type:'picture',
                            label:'图片1',
                            isShow:true,
                            style:'default'
                        }
                    }
                }
            ],
            dataInfo:{
                "name": "ewww",
                "address": "http://wufazhuce.com/one/2202",
                "more": "ewww",
                "picture": "http://image.wufazhuce.com/FpExIypm6ZcwSpZMv3RrPitIHHSI"
            },
            labelPosition:"top"
        }
    },
    methods: {
        hello() {
            alert('Hello World!');
        }
    }
  }
</script>


